:root {
  --font: "Baloo Tamma 2", cursive;
  --main-colour: #96c6e0;
  --secondary-colour: #67acc7;
  --gradient-colour: linear-gradient(to top, #4b778f, #6c95ad);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font);
}

h2 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
  font-weight: 400;
}

.container {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.container label {
  font-size: 2rem;
  color: var(--secondary-colour);
}
.container input {
  font-size: 1.5rem;
  outline: none;
  font-family: var(--font);
  padding: 0 0.3rem;
  height: 3rem;
  color: var(--secondary-colour);
}
.container .btn {
  color: var(--secondary-colour);
  padding: 0 0.6rem;
  font-size: 1.5rem;
  background-color: white;
  outline: none;
  border: none;
  cursor: pointer;
  height: 3rem;
  transition: all 0.3s ease-in-out;
}
.container .btn:hover {
  background: var(--secondary-colour);
  color: white;
}

.info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 90vh;
  width: 100vw;
  background: var(--gradient-colour);
}

.icon {
  width: 5rem;
  height: 5rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}